.SectionHalf{
    position: relative;
    width: 100%;
    border: 1px solid black;

    /* margin: 10px; */
    padding: 10px;

    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.SectionHalf >*{
    margin: 5px 0px;
}

.SectionHalf_buttons a{
    text-decoration: none;
    padding: 4px;
}

.sectionHalf_box3 img{
 width: 100%;
 height: auto;
}

@media(max-width:900px) {
    .SectionHalf{
        width: 100%;
    }
    .SectionHalf >*{
        margin: 0;
    }
}